@import '../../mixins/mixin.scss';

.slogan_banner{
  z-index: 1;
  color: #ffffff;
  position: relative;
  top: 20vh;
  overflow-x: hidden;
  -webkit-overflow-x: hidden !important;

  .span_slogan{
    text-align: center;
    font-size: 1.8em;
    display: flex;
    flex-direction: column;
    justify-content: flex-start !important;
    align-items: flex-start;
    padding-left: 1%;

  }

     .title_main{
      display: block;
      font-size: 1.6em;
      font-weight: bold;
    }
    
}
  
  .buttons{
    position: relative;
    @include flex(flex-start,center);
    z-index: 1;
    padding-top: 24vh;
    gap: 5vw;
    margin-left: 8px;
  }

  @media (min-width: 1800px){
   .title_main{
      display: block;
      font-size: 2.2em !important;
      font-weight: bold;
    }
    
    .sub_title_slogan{
      font-size: 1.3em;
    }
  }

  @media (max-width: 1200px){
    .title_main{
      font-size: 2px !important;
    }
  }

  @media only screen and (orientation: portrait){
    .slogan_banner{
      margin-top: 30vh;
      margin-left: 10%;
    }
    .span_slogan{
      font-size: 1em;  
    }

    .buttons{
      margin-left: 32%;
      padding-top: 21vh;
    }
  }
  
  @media (max-width: 780px){
    .span_slogan{
      font-size: 2.5em;
    }
  }


@media (max-width: 500px){
  .slogan_banner{
    color: black;
  }
  .buttons{
    gap: 10vw;
    padding-top: 24vh;
    margin-left: 15%;
    font-size: 1.3em;
  }

}

@media (max-width: 450px){

  .slogan_banner{
    margin-top: 12vh;
    margin-left: 6%;
  }
  .span_slogan{
    margin: 10px 0;
    font-size: 1.4em !important;  
  }

  .buttons{
    margin-left: 12%;
  }
}


